<template>
  <div>
    <el-radio label="" v-model="publishTimeType"></el-radio>
    <el-radio label="" v-model="publishTimeType"></el-radio>
    <div style="margin-top:5px;" v-if="publishTimeType === ''">
      <el-date-picker
        v-model="curTime"
        type="datetime"
        placeholder="" style="width: 280px;" @change="timeHandle">
      </el-date-picker>
    </div>
  </div>
</template>

<script>

  export default {
    data() {
      return {
        curTime: '',
        publishTimeType: ''
      }
    },
    props: {
      value: {
        type: String,
        default: ''
      }
    },
    watch: {
      publishTimeType: function (newVal, oldVal){
        if(newVal === ''){
          this.$emit('input', '')
          this.curTime = ''
        }
      }
    },
    
    created (){

      if(this.value) {
        this.curTime = this.value
        this.publishTimeType = ''
      }
    },
    methods: {
      timeHandle(value){

        this.$emit('input', value)
      }
    }
  }
</script>

<style lang="scss">

</style>


